*, *:before, *:after {
  box-sizing: border-box;
}

html, body, div, span, object, iframe, figure, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, code, em, img, small, strike, strong, sub, sup, tt, b, u, i, ol, ul, li, fieldset, form, label, table, caption, tbody, tfoot, thead, tr, th, td, main, canvas, embed, footer, header, nav, section, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  text-size-adjust: none;
}

footer, header, nav, section, main {
  display: block;
}

a {
  text-decoration: none;
}

h1 {
  font-size: 32pt;
}

body {
  font-family: "Inter", sans-serif;
  font-weight: normal;
  color: rgba(0, 0, 0, 0.8);
  font-size: 14px;
}

.btn {
  display: block;
  font-size: 12px;
  border: 2px solid;
  border-radius: 25px;
}

.order_btn {
  padding: 10px 40px;
  color: #212864;
  border-color: #212864;
  transition: 0.2s;
}

.order_btn:hover {
  color: white;
  background-color: #212864;
}

.upper_head {
  max-width: 90vw;
  display: flex;
  justify-content: space-between;
  margin: 0 auto;
  margin-top: 10px;
}

.logo p {
  font-size: 12px;
  color: rgba(0, 0, 0, 0.7);
  text-align: center;
}

.numbers,
.adress {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: flex-end;
}

.phone_number {
  font-size: 16px;
  font-weight: bold;
  color: #212864;
}

.social_icons {
  display: flex;
  justify-content: space-between;
  margin-top: 5px;
}

.nav {
  background-color: #15193D;
  height: 55px;
}

.nav_bar,
.nav_catalog {
  max-width: 88vw;
  margin: 0 auto;
  height: inherit;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
}

.nav_link {
  color: white;
  font-size: 14px;
  transition: 0.1s;
}

.nav_link:hover,
.nav_link:active {
  color: #FFC239;
}

.catalog {
  background-color: #8C93D1;
  height: 55px;
}
.catalog .vertical {
  height: 45px;
  width: 2px;
  background-color: rgba(255, 255, 255, 0.4);
}

.face {
  position: relative;
  background-color: rgba(0, 0, 0, 0.4);
  height: 600px;
  background-image: url(../img/head_background.png);
  background-repeat: no-repeat;
  background-size: cover;
  background-blend-mode: multiply;
}
.face .text {
  position: absolute;
  top: 10vh;
  left: 10vw;
}
.face .text h1 {
  max-width: 875px;
  color: white;
  font-size: 48px;
  font-weight: bold;
}
.face .text p {
  margin-top: 25px;
  font-family: "Montserrat";
  font-size: 16px;
  font-weight: 500;
  color: rgba(255, 255, 255, 0.8);
  max-width: 470px;
}
.face .text .calculate_group {
  margin-top: 30px;
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 10px;
}
.face .text .calculate_group a {
  color: white;
}
.face .text .calculate_group a #circle,
.face .text .calculate_group a #vector {
  transition: all 0, 1s ease;
}
.face .text .calculate_group a #Group_calculate {
  stroke: white;
}
.face .text .calculate_group a #Group_calculate:hover {
  stroke: #FFC239;
}
.face .text .calculate_group a:hover {
  color: #FFC239;
}

.tabs {
  max-width: 90vw;
  margin: 0 auto;
  margin-top: 80px;
}
.tabs h1 {
  font-size: 42px;
  font-weight: bold;
  margin-bottom: 20px;
}

.tabs__nav_btns {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  gap: 15px;
  margin-bottom: 20px;
}

.tabs__nav_btn {
  font-size: 20px;
  font-weight: 500;
  color: #212864;
  padding: 10px 30px;
}

.tabs__nav_btn.active {
  background-color: #FFC239;
}

.tabs__item {
  display: none;
}

.tabs__item.active {
  display: block;
}

.slider_card {
  width: 285px;
  height: 355px;
  border-radius: 10px;
  background-color: rgba(152, 144, 144, 0.31);
  margin: 0 auto;
}
.slider_card .card_photo {
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  width: 285px;
  height: 140px;
  overflow: hidden;
}
.slider_card .card_photo img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}
.slider_card .card_content {
  color: black;
  width: 85%;
  margin: 0 auto;
}
.slider_card .card_content h2 {
  font-size: 18px;
  font-weight: bold;
}
.slider_card .card_content table {
  margin-top: 15px;
  margin-bottom: 20px;
  width: 100%;
}
.slider_card .card_content table tr {
  display: inline-block;
  width: 100%;
}
.slider_card .card_content table tr td {
  display: inline-block;
  width: 50%;
  font-size: 12px;
  color: rgba(0, 0, 0, 0.8);
}
.slider_card .card_content .cost {
  font-weight: bold;
  font-size: 22px;
}

.swiper {
  max-width: 90vw;
}

.section_btn {
  margin: 0 auto;
  width: 310px;
  margin-top: 15px;
  margin-bottom: 20px;
}

.all_projects_btn {
  font-size: 16px;
  background-color: #212864;
  color: white;
  padding: 15px 30px;
  transition: 0.2s;
  text-align: center;
}

.all_projects_btn:hover {
  background-color: #FFC239;
  color: #212864;
  border: 1px solid #212864;
}